import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function filingDate(_tableName: string) {
  const title = "Filing date"
  return genericColumn({
    endpoint: "companyIncomeStatement",
    columnKeys: ["filingDate"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The date the report was filed.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyIncomeStatement?.data?.filingDate}
          queryResult={info.row.original.financialData.companyIncomeStatement}
        >
          {info.row.original.financialData.companyIncomeStatement?.data?.filingDate}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { filingDate }
